import React from 'react';
import axios from 'axios';
import './App.css';
import Movie from './Movie';

interface IMovie {
  id: number;
  title: string;
  year: number;
  summary: string;
  medium_cover_image: string;
  genres: string[];
}

interface IState {
  isLoading: boolean;
  movies: IMovie[];
}

class App extends React.Component {
  state: IState = {
    isLoading: true,
    movies: [],
  };

  getMovies = async () => {
    const {
      data: {
        data: {movies},
      },
    } = await axios.get(
      'https://yts-proxy.now.sh/list_movies.json?sort_by=rating',
    );
    this.setState({movies, isLoading: false});
  };
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const {isLoading, movies} = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.length > 0 &&
              movies.map(movie => (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  title={movie.title}
                  summary={movie.summary}
                  poster={movie.medium_cover_image}
                  genres={movie.genres}
                />
              ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
